"use client"

import { useEffect, useState } from "react"
import { useParams, useRouter } from "next/navigation"
import {
  ArrowLeft,
  Phone,
  MessageCircle,
  MapPin,
  Clock,
  Camera,
  CheckCircle,
  AlertTriangle,
  FileText,
  Send,
  Upload,
  Calendar,
  User,
  Smartphone,
  Navigation,
  Timer
} from "lucide-react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Textarea } from "@/components/ui/textarea"
import { Input } from "@/components/ui/input"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter
} from "@/components/ui/dialog"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue
} from "@/components/ui/select"
import { doc, getDoc, updateDoc, onSnapshot, arrayUnion, Timestamp, collection, addDoc, query, where, onSnapshot as onSnapshotCol } from "firebase/firestore"
import { db, auth, storage, isMockMode } from "@/firebase/firebaseConfig"
import { ref, uploadBytes, getDownloadURL } from "firebase/storage"
import { useLanguage } from "@/lib/i18n"
import { cn, logger } from "@/lib/utils"
import { getCurrentLocation, formatLocation } from "@/lib/gpsService"

// Mock Storage if not available
const mockUpload = async (file: File) => {
  return new Promise<string>((resolve) => {
    setTimeout(() => {
      resolve(URL.createObjectURL(file))
    }, 1000)
  })
}

export default function TechnicianOrderDetails() {
  const { id } = useParams()
  const router = useRouter()
  const { t, language } = useLanguage()
  const [order, setOrder] = useState<any>(null)
  const [loading, setLoading] = useState(true)
  const [note, setNote] = useState("")
  const [uploading, setUploading] = useState(false)
  const [statusDialogOpen, setStatusDialogOpen] = useState(false)
  const [newStatus, setNewStatus] = useState("")
  const [etaMinutes, setEtaMinutes] = useState<string>("")
  const [reqDialogOpen, setReqDialogOpen] = useState(false)
  const [reqName, setReqName] = useState("")
  const [reqCategory, setReqCategory] = useState<"spare_part" | "additional_service" | "">("")
  const [reqQty, setReqQty] = useState<string>("1")
  const [reqPrice, setReqPrice] = useState<string>("")
  const [reqReason, setReqReason] = useState("")
  const [reqPhoto, setReqPhoto] = useState<string>("")
  const [hasPendingRequests, setHasPendingRequests] = useState(false)
  const [checkingIn, setCheckingIn] = useState(false)
  const [checkedIn, setCheckedIn] = useState(false)

  useEffect(() => {
    if (!id) return

    setLoading(true)

    if (isMockMode) {
      setOrder({
        id,
        device: "Device",
        issue: "Issue",
        status: "pending",
        customerPhone: "",
        location: "",
        createdAt: new Date().toISOString()
      })
      setHasPendingRequests(false)
      setLoading(false)
      return
    }

    const docRef = doc(db, "orders", id as string)
    const unsubscribe = onSnapshot(docRef, (docSnap) => {
      if (docSnap.exists()) {
        setOrder({ id: docSnap.id, ...docSnap.data() })
      }
      setLoading(false)
    }, (error) => {
      logger.error("Order subscription error:", error)
      setLoading(false)
    })

    const reqQ = query(collection(db, "tech_requests"), where("orderId", "==", id), where("status", "==", "pending"))
    const unsubReq = onSnapshotCol(reqQ, (snap) => {
      setHasPendingRequests(snap.size > 0)
    }, (error) => {
      logger.error("Tech requests subscription error:", error)
    })

    return () => { unsubscribe(); unsubReq() }
  }, [id])

  const handleStatusUpdate = async () => {
    if (!newStatus || !order) return

    // Prevent completion if there are pending requests
    if (newStatus === "completed" && hasPendingRequests) {
      alert(t("Pending Admin Approval"))
      return
    }

    if (isMockMode) {
      setOrder((prev: any) => {
        if (!prev) return prev
        const timeline = Array.isArray(prev.timeline) ? prev.timeline : []
        return {
          ...prev,
          status: newStatus,
          timeline: [...timeline, { status: newStatus, time: new Date().toISOString() }],
          updatedAt: new Date().toISOString()
        }
      })
      setStatusDialogOpen(false)
      return
    }

    const u = auth.currentUser
    if (!u || (order.technicianId && order.technicianId !== u.uid)) {
      alert(t("Missing or insufficient permissions"))
      return
    }

    try {
      const docRef = doc(db, "orders", id as string)
      await updateDoc(docRef, {
        status: newStatus,
        timeline: arrayUnion({
          status: newStatus,
          time: new Date().toISOString()
        }),
        updatedAt: Timestamp.now()
      })
      setStatusDialogOpen(false)
    } catch (error) {
      alert(t("Status update failed"))
    }
  }

  const handleSubmitRequest = async () => {
    if (!order || !reqName || !reqCategory || !reqQty || !reqPrice || !reqReason) return
    const qty = parseInt(reqQty, 10)
    const price = parseFloat(reqPrice)
    if (isNaN(qty) || qty <= 0 || isNaN(price) || price < 0) return

    if (isMockMode) {
      setHasPendingRequests(true)
      setReqDialogOpen(false)
      setReqName(""); setReqCategory(""); setReqQty("1"); setReqPrice(""); setReqReason(""); setReqPhoto("")
      alert(t("Pending Admin Approval"))
      return
    }

    const u = auth.currentUser
    if (!u || (order.technicianId && order.technicianId !== u.uid)) {
      alert(t("Missing or insufficient permissions"))
      return
    }

    try {
      await addDoc(collection(db, "tech_requests"), {
        orderId: order.id,
        technicianId: order.technicianId,
        technicianName: order.technicianName,
        partOrServiceName: reqName,
        category: reqCategory,
        quantity: qty,
        estimatedPrice: price,
        reason: reqReason,
        photoUrl: reqPhoto || null,
        status: "pending",
        createdAt: Timestamp.now(),
        history: [{ action: "submitted", by: "technician", at: Timestamp.now(), note: reqReason }]
      })

      // Optional: set order status to waiting_parts
      const docRef = doc(db, "orders", order.id)
      await updateDoc(docRef, {
        status: order.status === "in_progress" ? "waiting_parts" : order.status,
        pendingRequestsCount: (order.pendingRequestsCount || 0) + 1,
        updatedAt: Timestamp.now()
      })

      setReqDialogOpen(false)
      setReqName(""); setReqCategory(""); setReqQty("1"); setReqPrice(""); setReqReason(""); setReqPhoto("")
      alert(t("Pending Admin Approval"))
    } catch (error) {
      alert(t("Submit request failed"))
    }
  }

  const handleAddNote = async () => {
    if (!note.trim() || !order) return

    const newNote = {
      text: note,
      time: new Date().toISOString(),
      user: "Technician"
    }

    if (isMockMode) {
      setOrder((prev: any) => {
        if (!prev) return prev
        const notes = Array.isArray(prev.notes) ? prev.notes : prev.notes ? Object.values(prev.notes) : []
        return { ...prev, notes: [...notes, newNote] }
      })
      setNote("")
      return
    }

    const u = auth.currentUser
    if (!u || (order.technicianId && order.technicianId !== u.uid)) {
      alert(t("Missing or insufficient permissions"))
      return
    }

    try {
      const docRef = doc(db, "orders", id as string)
      await updateDoc(docRef, {
        notes: arrayUnion(newNote)
      })
      setNote("")
    } catch (error) {
      alert(t("Add note failed"))
    }
  }

  const handleFileUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files || !e.target.files[0]) return
    setUploading(true)
    const file = e.target.files[0]

    try {
      let downloadURL = ""
      if (!storage) {
        downloadURL = await mockUpload(file)
      } else {
        if (isMockMode) {
          downloadURL = await mockUpload(file)
        } else {
          const u = auth.currentUser
          if (!u || (order?.technicianId && order.technicianId !== u.uid)) {
            alert(t("Missing or insufficient permissions"))
            setUploading(false)
            return
          }
          const storageRef = ref(storage, `orders/${id}/${file.name}`)
          await uploadBytes(storageRef, file)
          downloadURL = await getDownloadURL(storageRef)
        }
      }

      const newAttachment = {
        url: downloadURL,
        name: file.name,
        type: file.type,
        time: new Date().toISOString()
      }

      if (isMockMode) {
        setOrder((prev: any) => {
          if (!prev) return prev
          const attachments = Array.isArray(prev.attachments) ? prev.attachments : prev.attachments ? Object.values(prev.attachments) : []
          return { ...prev, attachments: [newAttachment, ...attachments] }
        })
      } else {
        const docRef = doc(db, "orders", id as string)
        await updateDoc(docRef, {
          attachments: arrayUnion(newAttachment)
        })
      }
    } catch (error) {
      alert(t("File upload failed"))
    } finally {
      setUploading(false)
    }
  }
  const handleEtaSave = async () => {
    if (!etaMinutes || !order) return
    const minutes = parseInt(etaMinutes, 10)
    if (isNaN(minutes) || minutes < 0) return
    const iso = new Date(Date.now() + minutes * 60000).toISOString()

    if (isMockMode) {
      setOrder((prev: any) => ({ ...prev, estimatedCompletion: iso, updatedAt: new Date().toISOString() }))
      setEtaMinutes("")
      return
    }

    const u = auth.currentUser
    if (!u || (order.technicianId && order.technicianId !== u.uid)) {
      alert(t("Missing or insufficient permissions"))
      return
    }

    try {
      const docRef = doc(db, "orders", id as string)
      await updateDoc(docRef, { estimatedCompletion: iso, updatedAt: Timestamp.now() })
      setEtaMinutes("")
    } catch (error) {
      alert(t("ETA save failed"))
    }
  }

  const handleCheckIn = async () => {
    setCheckingIn(true)
    try {
      const location = await getCurrentLocation()
      if (isMockMode) {
        setOrder((prev: any) => {
          if (!prev) return prev
          const notes = Array.isArray(prev.notes) ? prev.notes : prev.notes ? Object.values(prev.notes) : []
          return {
            ...prev,
            checkIn: {
              lat: location.lat,
              lng: location.lng,
              address: location.address || "",
              timestamp: new Date().toISOString()
            },
            notes: [...notes, {
              text: `Checked in at: ${location.address || `${location.lat}, ${location.lng}`}`,
              time: new Date().toISOString(),
              user: "Technician"
            }],
            updatedAt: new Date().toISOString()
          }
        })
        setCheckedIn(true)
        alert(t("Check-in successful!"))
      } else {
        const u = auth.currentUser
        if (!u || (order?.technicianId && order.technicianId !== u.uid)) {
          alert(t("Missing or insufficient permissions"))
          setCheckingIn(false)
          return
        }
        const docRef = doc(db, "orders", id as string)
        await updateDoc(docRef, {
          checkIn: {
            lat: location.lat,
            lng: location.lng,
            address: location.address || "",
            timestamp: Timestamp.now()
          },
          notes: arrayUnion({
            text: `Checked in at: ${location.address || `${location.lat}, ${location.lng}`}`,
            time: new Date().toISOString(),
            user: "Technician"
          }),
          updatedAt: Timestamp.now()
        })
        setCheckedIn(true)
        alert(t("Check-in successful!"))
      }
    } catch (error: any) {
      alert(t("Check-in failed: ") + error.message)
    } finally {
      setCheckingIn(false)
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case "pending": return "text-yellow-500 bg-yellow-500/10"
      case "in_progress": return "text-blue-500 bg-blue-500/10"
      case "on_way": return "text-purple-500 bg-purple-500/10"
      case "completed": return "text-green-500 bg-green-500/10"
      default: return "text-gray-500 bg-gray-500/10"
    }
  }

  if (loading) return <div className="flex items-center justify-center min-h-screen text-white/50">{t("Loading Order Details...")}</div>
  if (!order) return <div className="flex items-center justify-center min-h-screen text-white/50">{t("Order not found")}</div>

  return (
    <div className="space-y-6 pb-20 animate-in fade-in duration-500">
      {/* Header */}
      <div className="flex items-center gap-4">
        <Button variant="ghost" size="icon" onClick={() => router.back()} className="text-white hover:bg-white/10">
          <ArrowLeft className="w-6 h-6" />
        </Button>
        <div>
          <h1 className="text-2xl font-bold text-white">{t("Order")} #{order.id}</h1>
          <p className="text-white/60 text-sm">{t("Assigned to you")}</p>
        </div>
        <div className="ml-auto">
          <Badge className={cn("px-3 py-1 text-sm capitalize border-0", getStatusColor(order.status))}>
            {order.status.replace('_', ' ')}
          </Badge>
        </div>
      </div>

      {/* Quick Actions Card */}
      <Card className="bg-white/5 border-white/10 backdrop-blur-sm">
        <CardContent className="p-4 flex flex-wrap justify-between items-center gap-2">
          <Button
            className={cn("flex-1 min-w-[80px] gap-2", checkedIn || order.checkIn ? "bg-green-600" : "bg-cyan-600 hover:bg-cyan-700")}
            onClick={handleCheckIn}
            disabled={checkingIn || checkedIn || order.checkIn}
          >
            <MapPin className="w-4 h-4" />
            <span className="hidden sm:inline">{checkedIn || order.checkIn ? t("Checked In") : t("Check In")}</span>
          </Button>
          <Button className="flex-1 min-w-[80px] bg-green-600 hover:bg-green-700 text-white gap-2" onClick={() => window.open(`tel:${order.customerPhone}`)}>
            <Phone className="w-4 h-4" /> <span className="hidden sm:inline">{t("Call")}</span>
          </Button>
          <Button className="flex-1 min-w-[80px] bg-green-500 hover:bg-green-600 text-white gap-2" onClick={() => window.open(`https://wa.me/${order.customerPhone?.replace('+', '')}`)}>
            <MessageCircle className="w-4 h-4" /> <span className="hidden sm:inline">{t("WhatsApp")}</span>
          </Button>
          <Button className="flex-1 min-w-[80px] bg-blue-600 hover:bg-blue-700 text-white gap-2" onClick={() => window.open(`https://maps.google.com/?q=${order.locationCoords?.lat},${order.locationCoords?.lng}`)}>
            <Navigation className="w-4 h-4" /> <span className="hidden sm:inline">{t("Navigate")}</span>
          </Button>
        </CardContent>
      </Card>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Left Column: Details */}
        <div className="lg:col-span-2 space-y-6">
          {/* Device Info */}
          <Card className="bg-white/5 border-white/10">
            <CardHeader>
              <CardTitle className="text-white flex items-center gap-2">
                <Smartphone className="w-5 h-5 text-cyan-500" />
                {t("Device Details")}
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div>
              <p className="text-white/40 text-xs uppercase">{t("Device")}</p>
              <p className="text-white font-medium">{order.device}</p>
            </div>
            <div>
              <p className="text-white/40 text-xs uppercase">{t("Issue")}</p>
              <p className="text-white font-medium">{order.issue}</p>
            </div>
            <div>
              <p className="text-white/40 text-xs uppercase">{t("Price")}</p>
              <p className="text-cyan-400 font-bold">{order.price} AED</p>
            </div>
            <div>
              <p className="text-white/40 text-xs uppercase">{t("Date")}</p>
              <p className="text-white font-medium">{new Date(order.date || (order.createdAt?.toDate?.() || order.createdAt)).toLocaleDateString()}</p>
            </div>
          </div>
            </CardContent>
          </Card>

          {/* Customer Info */}
          <Card className="bg-white/5 border-white/10">
            <CardHeader>
              <CardTitle className="text-white flex items-center gap-2">
                <User className="w-5 h-5 text-cyan-500" />
                {t("Customer Info")}
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-start gap-3">
                <User className="w-5 h-5 text-white/40 mt-1" />
                <div>
                  <p className="text-white font-medium">{order.customerName}</p>
                  <p className="text-white/60 text-sm">{t("Customer")}</p>
                </div>
              </div>
              <div className="flex items-start gap-3">
                <MapPin className="w-5 h-5 text-white/40 mt-1" />
                <div>
                  <p className="text-white font-medium">{order.location}</p>
                  <p className="text-white/60 text-sm">{t("Location")}</p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Timeline */}
          <Card className="bg-white/5 border-white/10">
            <CardHeader>
              <CardTitle className="text-white flex items-center gap-2">
                <Clock className="w-5 h-5 text-cyan-500" />
                {t("Timeline")}
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="relative border-l border-white/10 ml-2 space-y-6 pl-6">
                {order.timeline?.map((event: any, index: number) => (
                  <div key={index} className="relative">
                    <div className="absolute -left-[29px] top-1 h-3 w-3 rounded-full bg-cyan-500 ring-4 ring-black" />
                    <p className="text-white font-medium capitalize">{event.status.replace('_', ' ')}</p>
                    <p className="text-white/40 text-xs">{new Date(event.time).toLocaleString()}</p>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>

          {/* Invoice */}
          {order.invoiceItems?.length ? (
            <Card className="bg-white/5 border-white/10">
              <CardHeader>
                <CardTitle className="text-white">{t("Invoice")}</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2 text-sm">
                  {order.invoiceItems.map((item: any, i: number) => (
                    <div key={i} className="flex items-center justify-between border-b border-white/10 py-2">
                      <div>
                        <div className="text-white">{item.description}</div>
                        <div className="text-white/40 text-xs">{t("Quantity")}: {item.quantity}</div>
                      </div>
                      <div className="text-white/80">
                        AED {Number(item.unitPrice).toFixed(2)}
                      </div>
                    </div>
                  ))}
                  <div className="flex items-center justify-between pt-3">
                    <div className="text-white font-semibold">{t("Total")}</div>
                    <div className="text-cyan-400 font-bold">AED {Number(order.totalCost || order.price || 0).toFixed(2)}</div>
                  </div>
                </div>
              </CardContent>
            </Card>
          ) : null}
        </div>

        {/* Right Column: Actions */}
        <div className="space-y-6">
          {/* Request Additional Parts / Services */}
          <Card className="bg-white/5 border-white/10">
            <CardHeader>
              <CardTitle className="text-white">{t("Request Additional Parts / Services")}</CardTitle>
            </CardHeader>
            <CardContent>
              <Dialog open={reqDialogOpen} onOpenChange={setReqDialogOpen}>
                <DialogTrigger asChild>
                  <Button className="w-full bg-purple-500 hover:bg-purple-600 text-black font-bold">
                    {t("Request Additional Parts / Services")}
                  </Button>
                </DialogTrigger>
                <DialogContent className="bg-zinc-900 border-white/10 text-white max-w-lg">
                  <DialogHeader>
                    <DialogTitle>{t("Request Additional Parts / Services")}</DialogTitle>
                  </DialogHeader>
                  <div className="space-y-3">
                    <div>
                      <p className="text-xs text-white/60 mb-1">{t("Part / Service Name")}</p>
                      <Input value={reqName} onChange={(e) => setReqName(e.target.value)} className="bg-black/20 border-white/10 text-white" />
                    </div>
                    <div>
                      <p className="text-xs text-white/60 mb-1">{t("Category")}</p>
                      <Select onValueChange={(v) => setReqCategory(v as any)}>
                        <SelectTrigger className="bg-black/20 border-white/10 text-white">
                          <SelectValue placeholder={t("Category")} />
                        </SelectTrigger>
                        <SelectContent className="bg-zinc-900 border-white/10 text-white">
                          <SelectItem value="spare_part">{t("Spare Part")}</SelectItem>
                          <SelectItem value="additional_service">{t("Additional Service")}</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div className="grid grid-cols-2 gap-3">
                      <div>
                        <p className="text-xs text-white/60 mb-1">{t("Quantity")}</p>
                        <Input type="number" value={reqQty} onChange={(e) => setReqQty(e.target.value)} className="bg-black/20 border-white/10 text-white" />
                      </div>
                      <div>
                        <p className="text-xs text-white/60 mb-1">{t("Estimated Price (AED)")}</p>
                        <Input type="number" value={reqPrice} onChange={(e) => setReqPrice(e.target.value)} className="bg-black/20 border-white/10 text-white" />
                      </div>
                    </div>
                    <div>
                      <p className="text-xs text-white/60 mb-1">{t("Reason for Request")}</p>
                      <Textarea value={reqReason} onChange={(e) => setReqReason(e.target.value)} className="bg-black/20 border-white/10 text-white" />
                    </div>
                    <div>
                      <p className="text-xs text-white/60 mb-1">{t("Optional Photo")}</p>
                      {/* Simple image upload using storage or mock */}
                      <Input type="file" accept="image/*" onChange={async (e) => {
                        const f = e.target.files?.[0]
                        if (!f) return
                        try {
                          if (!storage) {
                            const url = await mockUpload(f)
                            setReqPhoto(url)
                          } else {
                            const storageRef = ref(storage, `requests/${order.id}/${Date.now()}_${f.name}`)
                            await uploadBytes(storageRef, f)
                            const url = await getDownloadURL(storageRef)
                            setReqPhoto(url)
                          }
                        } catch (err) { logger.error(err) }
                      }} className="bg-black/20 border-white/10 text-white" />
                      {reqPhoto && (
                        <div className="mt-2 w-24 h-24 rounded-lg overflow-hidden border border-white/10">
                          <img src={reqPhoto} alt="Photo" className="w-full h-full object-cover" />
                        </div>
                      )}
                    </div>
                  </div>
                  <DialogFooter>
                    <Button onClick={handleSubmitRequest} className="bg-cyan-500 text-black">{t("Submit Request")}</Button>
                  </DialogFooter>
                </DialogContent>
              </Dialog>
              {hasPendingRequests && (
                <p className="mt-3 text-xs text-yellow-400">{t("Pending Admin Approval")}</p>
              )}
            </CardContent>
          </Card>
          {/* Status Update */}
          <Card className="bg-white/5 border-white/10">
            <CardHeader>
              <CardTitle className="text-white">{t("Update Status")}</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <Dialog open={statusDialogOpen} onOpenChange={setStatusDialogOpen}>
                <DialogTrigger asChild>
                  <Button className="w-full bg-cyan-500 hover:bg-cyan-600 text-black font-bold">
                    {t("Change Status")}
                  </Button>
                </DialogTrigger>
                <DialogContent className="bg-zinc-900 border-white/10 text-white">
                  <DialogHeader>
                    <DialogTitle>{t("Update Order Status")}</DialogTitle>
                  </DialogHeader>
                  <Select onValueChange={setNewStatus} defaultValue={newStatus}>
                    <SelectTrigger className="bg-black/20 border-white/10">
                      <SelectValue placeholder={t("Select new status")} />
                    </SelectTrigger>
                    <SelectContent className="bg-zinc-900 border-white/10 text-white">
                      <SelectItem value="on_way">{t("On the Way")}</SelectItem>
                      <SelectItem value="in_progress">{t("In Progress")}</SelectItem>
                      <SelectItem value="waiting_parts">{t("Waiting for Parts")}</SelectItem>
                      <SelectItem value="completed">{t("Completed")}</SelectItem>
                    </SelectContent>
                  </Select>
                  <DialogFooter>
                    <Button onClick={handleStatusUpdate} className="bg-cyan-500 text-black">{t("Update")}</Button>
                  </DialogFooter>
                </DialogContent>
              </Dialog>

              {/* Countdown Timer */}
              {order.estimatedCompletion && (
                <CountdownTimer targetDate={order.estimatedCompletion} />
              )}
              <div className="space-y-2 mt-3">
                <p className="text-sm text-white/60">{t("ETA (minutes)")}</p>
                <div className="flex gap-2">
                  <Input
                    type="number"
                    value={etaMinutes}
                    onChange={(e) => setEtaMinutes(e.target.value)}
                    className="bg-black/20 border-white/10 text-white"
                  />
                  <Button onClick={handleEtaSave} className="bg-white/10 hover:bg-white/20">
                    {t("Save")}
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Notes & Attachments */}
          <Card className="bg-white/5 border-white/10">
            <CardHeader>
              <CardTitle className="text-white">{t("Work Notes")}</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2 max-h-40 overflow-y-auto pr-2">
                {(() => {
                  const notes = Array.isArray(order.notes)
                    ? order.notes
                    : order.notes
                    ? Object.values(order.notes)
                    : [];
                  return notes.map((n: any, i: number) => (
                  <div key={i} className="bg-white/5 p-2 rounded text-sm">
                    <p className="text-white/90">{n.text}</p>
                    <p className="text-white/30 text-[10px] text-right mt-1">{new Date(n.time?.toDate?.() || n.time).toLocaleTimeString()}</p>
                  </div>
                  ))
                })()}
              </div>
              <div className="flex gap-2">
                <Input
                  placeholder={t("Add a note...")}
                  value={note}
                  onChange={(e) => setNote(e.target.value)}
                  className="bg-black/20 border-white/10 text-white"
                />
                <Button size="icon" onClick={handleAddNote} className="bg-white/10 hover:bg-white/20">
                  <Send className="w-4 h-4" />
                </Button>
              </div>

              <div className="pt-4 border-t border-white/10">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-sm font-medium text-white">{t("Attachments")}</span>
                  <label className="cursor-pointer bg-white/10 hover:bg-white/20 p-2 rounded-full transition-colors">
                    <Upload className="w-4 h-4 text-cyan-500" />
                    <input type="file" className="hidden" accept="image/*" onChange={handleFileUpload} disabled={uploading} />
                  </label>
                </div>
                <div className="grid grid-cols-3 gap-2">
                  {(() => {
                    const attachments = Array.isArray(order.attachments)
                      ? order.attachments
                      : order.attachments
                      ? Object.values(order.attachments)
                      : [];
                    return attachments.map((att: any, i: number) => (
                    <a key={i} href={att.url} target="_blank" rel="noopener noreferrer" className="block aspect-square rounded bg-white/5 overflow-hidden border border-white/10 hover:border-cyan-500/50 transition-all">
                      <img src={att.url} alt="Attachment" className="w-full h-full object-cover" />
                    </a>
                    ))
                  })()}
                  {uploading && (
                    <div className="aspect-square rounded bg-white/5 flex items-center justify-center animate-pulse">
                      <Camera className="w-5 h-5 text-white/30" />
                    </div>
                  )}
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}

function CountdownTimer({ targetDate }: { targetDate: string }) {
  const { t } = useLanguage()
  const [timeLeft, setTimeLeft] = useState("")
  const [status, setStatus] = useState<"normal" | "urgent" | "expired">("normal")

  useEffect(() => {
    const calculateTime = () => {
      const now = new Date().getTime()
      const target = new Date(targetDate).getTime()
      const diff = target - now

      if (diff <= 0) {
        setTimeLeft("00:00:00")
        setStatus("expired")
        return
      }

      const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60))
      const seconds = Math.floor((diff % (1000 * 60)) / 1000)

      setTimeLeft(`${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`)

      if (hours === 0 && minutes < 30) setStatus("urgent")
      else setStatus("normal")
    }

    calculateTime()
    const timer = setInterval(calculateTime, 1000)
    return () => clearInterval(timer)
  }, [targetDate])

  return (
    <div className={cn("p-3 rounded bg-white/5 border flex items-center gap-3 transition-colors",
      status === "expired" ? "border-red-500/50 bg-red-500/10" :
        status === "urgent" ? "border-yellow-500/50 bg-yellow-500/10" :
          "border-white/10"
    )}>
      <Timer className={cn("w-5 h-5",
        status === "expired" ? "text-red-500" :
          status === "urgent" ? "text-yellow-500" :
            "text-cyan-500"
      )} />
      <div>
        <p className="text-xs text-white/60">{t("Time Remaining")}</p>
        <p className="text-lg font-mono font-bold text-white tracking-wider">
          {timeLeft}
        </p>
      </div>
    </div>
  )
}
